const recorderManager = uni.getRecorderManager();
export const __textInput = {
	InputFocus(e) {
		this.ifImgChoose = false;
	},
	keyboardChange(e) {
		if (this.InputBottom > 0 && e.detail.height > this.InputBottom) return; //
		this.InputBottom = e.detail.height;
		if (this.mode === 'aboutChat') {
			uni.$emit("keyboardChange", e.detail.height);
		}
	},
	ImgChooseHandle() {
		if (this.mode === 'aboutNews') {
			this.ifImgChoose = !this.ifImgChoose;
		} else if (this.mode === 'aboutChat') {
			let TIM = this.TIM;
			let tim = this.tim;
			uni.chooseImage({
				count: 1,
				sizeType: ['original', 'compressed'], // 
				sourceType: ['album'], // 
				success: (res) => {
					let message = tim.createImageMessage({
						to: this.userIdTo,
						conversationType: TIM.TYPES.CONV_C2C,
						payload: { file: res }
					});
					// 2. 
					let promise = tim.sendMessage(message);
					promise.then(function(imResponse) {
						// 
						uni.$emit("reciveChatMsg", [imResponse.data.message]);
					}).catch(function(imError) {
						// 
					console.warn('sendMessage error:', imError);
			});
  }
});
		}
	},
	imgSelector() {
		this.$api.imgHandle.imgSelector.call(this);
	},
	imgRemove(index) {
		this.$api.imgHandle.removeCurrentImg.call(this, index);
	},
	// 
	async publishHandle() {
		if (this.inputContent.trim().length === 0) {
			return uni.showToast({
				title: "~",
				icon: "none"
			});
		}
		
		// 
		for (const img of this.imgList) {
			const newImg = await this.$http.upLoadFile(img.url);
			this.$set(img, 'url', newImg);
		console.log(img);
		}		
		
		let finalId = undefined;
		let data = {
			url: '',
			method: 'POST',
			data: {
				content: this.inputContent,
				images: this.imgList
			}
		}
		// 
		if (this.type === 'news' || this.type === 'comment') {
			data.url = '/dynamicComment/pushComment';
			if (this.type === 'news') {
				data.data.dynamicId = this.target.id;
			} else {
				data.data.rootCommentId = this.target.id;
			}
			finalId = this.target.id;
		} else {
			data.data = 
				{
					...data.data, 
					parentCommentId: this.target.id,
					parentCommentUserId: this.target.userId,
					rootCommentId: this.target.rootCommentId
				}
			data.url = '/dynamicComment/replyComment';
			finalId = this.target.rootCommentId;
		}
		// 
		const result = await this.$http.request(data);
		// pop
		uni.$emit('updateCurrentInfo', '');
		if (this.type !== 'news') {
			uni.$emit('repylChange', { type: this.type, data: this.target, popDontChange: true, id: finalId });
		}
		this.clearCurrentInfo();
		if (result.data.code === 200) {
			uni.showToast({
				title: '~',
				icon: 'none'
			});
		}
	},
	// 
	chatHandle() {
		if (this.inputContent.trim().length === 0) {
			return uni.showToast({
				title: "~",
				icon: "none"
			});
		}
		let TIM = this.TIM;
		let tim = this.tim;
		// Web 
		// 1. 
		let message = tim.createTextMessage({
		  to: this.userIdTo,
		  conversationType: TIM.TYPES.CONV_C2C,
		  payload: {
		    text: this.inputContent
		  },
		});
		// 2. 
		let promise = tim.sendMessage(message);
		promise.then(function(imResponse) {
		  // 
			uni.$emit("reciveChatMsg", [imResponse.data.message]);
		}).catch(function(imError) {
		  // 
		  console.warn('sendMessage error:', imError);
		});
		this.clearCurrentInfo();
	},
	// 
	clearCurrentInfo() {
		this.inputContent = '';
		this.ifImgChoose = false;
		this.imgList = [];
		this.$forceUpdate();
	},
	// 
	changeRecordState() {
		this.ifRecord = !this.ifRecord;
		this.InputBottom = 0;
		if (this.ifRecord) {
			uni.authorize({
					scope: 'scope.record',
					success: (res) => {
						//  
					},
					fail: (res) => {
						//  
						this.toSettingPage({
							success: (authRes) => {
								let authState = authRes.authSetting['scope.record'];
								this.ifRecord = authState;
							},
							fail: (err) => {
								this.ifRecord = false;
							}
						});
					}
			})
		}
	},
	// 
	longpressRecordStart(e) {
		this.recordStartPoint = e.changedTouches[0];
		if (e.target.id === 'record') {
			uni.authorize({
					scope: 'scope.record',
					success: (res) => {
						//  
						recorderManager.start();
						this.recordTitle = '',
						this.isRecording = true;
						this.ifRecordSucc = true;
					},
					fail: (res) => {
						//   
						this.toSettingPage({
							success: (authRes) => {
								let authState = authRes.authSetting['scope.record'];
								this.ifRecord = authState;
							},
							fail: (err) => {
								this.ifRecord = false;
							}
						});
					}
			});
		}
	},
	// 
	recordTouchMove(e) {
		if (this.isRecording) {
			let point = e.changedTouches[0];
			if (this.recordStartPoint.clientY - point.clientY > 100) {
				this.recordTitle = "";
				this.ifRecordSucc = false;
			} else if (this.recordStartPoint.clientY - point.clientY > 20) {
				this.recordTitle = "";
				this.ifRecordSucc = true;
			} else {
				this.recordTitle = "";
				this.ifRecordSucc = true;
			}
		}
	},
	// 
	recordEnd() {
		this.isRecording = false;
		recorderManager.stop();
		this.recordTitle = '';
	},
	// 
	toSettingPage(options = {}) {
		uni.showModal({
			title: '',
			content: '',
			cancelText: '',
			confirmText: '',
			success: (res) => {
				if (res.confirm) {
					uni.openSetting(options);
				} else if(res.cancel) {
					this.ifRecord = false;
				}
			},
			fail:(err) => {
				this.ifRecord = false;
			}
		})
	}
}